import inView from 'in-view'

export default function () {
inView('h2')
    .on('enter', el => {
        el.style.opacity = 1;
    })
    .on('exit', el => {
        el.style.opacity = 0;
    });
}

const body = document.querySelector("body")
document.addEventListener("DOMContentLoaded", function() {
    body.style.opacity = '1';
});